import React from "react";
import "./Form.css";

function Form({ value, onChange, onCreate, onKeyPress }) {
  return (
    <div className="form">
      <input value={value} onChange={onChange} onKeyPress={onKeyPress}></input>
      <div className="create__btn" onClick={onCreate}>
        추가
      </div>
    </div>
  );
}

export default Form;
